const Base = require('../base.js');
//获取文章
module.exports = class extends Base {
    async indexAction() {
        let model = this.model('article');
        let data = ""
        if (this.ctx.post('id')) {
            data = await model.where({
                id: this.ctx.post('id')
            }).find();
        } else {
            data = await model.field('article.id,article.title,article.addtime,c.user_name').join({
                table: 'user_info',
                join: 'inner', //join 方式，有 left, right, inner 3 种方式
                as: 'c', // 表别名
                on: ['userId', 'id'] //ON 条件
            }).page(this.ctx.post('page')).order('id DESC').countSelect()
        }

        return this.fail(data); //2输出json 推荐
    }

}
